export default () => {
  return (
    <div>
      <div className="container">
        <div className="about"></div>
        <div className="get-post"></div>
        <div className="contact"></div>
      </div>
      <style jsx>{`
        .container {
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
        }
        .about {
          position: absolute;
          width: 50%;
          height: 100%;
          left: 0;
          top: 0;
          background-color: #FA987D;
          transform: skew(-10deg);
        }
        .contact {
          position: absolute;
          width: 50%;
          height: 100%;
          top: 0;
          left: 50%;
          background-color: #10A296;
        }
      `}</style>
    </div>
  );
}



